</div>


</template>
<style>
  .menu {
    min-width: 100px;
    width: 70%;
    max-width: 320px;
  }
  .content {
    width: 100%;
  }
.refresh{
    width:30px;
    height: 30px;
    line-height: 30px;
    bottom: 50px;
    right: 30px;
    z-index: 10;
    position: fixed;
    cursor: pointer;
}
.category{
    width:30px;
    height: 30px;
    line-height: 30px;
    bottom: 10px;
    right: 30px;
    z-index: 10;
    position: fixed;
    cursor: pointer;
} 
.article-title{
  
    line-height: 1.2;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    font-size: 15px;
    color: #000;

}
.article-tags{
margin-top: 10px
}
</style>
<script>

import InfiniteLoading from 'vue-infinite-loading'
import api from '../api/api.js';
import ListItem from './ListItem';

export default {
  
  name: 'ArticleList',
  
  
    components: {
      InfiniteLoading,
      ListItem
    },

    props: ['cate'],

    data () {
      return {
        // swiperOption: {
        //   slidesPerView: 'auto',
        //   initialSlide: 0,
        //   resistanceRatio: .00000000000001,
        //   slideToClickedSlide: true
        // },
        articles: [],
        distance: 200,
        page:0,
        showload:false,
        rank:0, // 当前下拉文章最低rank
      }
    },
    mounted() {
      this.articles = JSON.parse(window.localStorage.getItem("articles"))||[]
      this.rank = parseFloat(window.localStorage.getItem("rank")) || 0
      // alert(this.cate)
    },
    methods: {



        refresh:function(){
          var site = this
          setTimeout(function(){
            site.articles= []
            site.rank=0
            site.$refs.infiniteLoading.stateChanger.reset()
          }, 50);
          return true
        },

        infiniteHandler: function ($state) {

          if (this.articles.length > 500) {
            $state.complete();
            // console.log("dont...");
          } else {
              var site = this

              var uri = '/hot?limit=10&rank='+site.rank+'&tag='+this.cate;

              setTimeout(function(){
                api.get(uri,function(err,data){
                  if(data.length>0){
                    for(var t=0;t<data.length;t++){
                      site.articles.push(data[t])
                    }
                    site.rank = data[(data.length-1)].Rank
                    $state.loaded();
                    site.page ++
                  }else{
                    $state.complete();
                  }
                })
              }, 200);
            // }.bind(this), 1000);
          }
        },

    },


    watch:{
      articles:function(){
        localStorage.setItem("articles",JSON.stringify(this.articles))
        localStorage.setItem("rank",this.rank)
      },
      cate:function(){
          var __cate = parseInt(window.localStorage.getItem("__cate")) || 0
          // console.log(__cate,this.cate)
          if( __cate!=parseInt(this.cate) ){
            localStorage.setItem("__cate",this.cate)
            // console.log('refresh...',this.cate)
            this.refresh()
          }

      }
    }
}
</script>






